@import url("https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

html {
  overflow-x: hidden;
}

body {
  color: #000 !important;
  overflow-x: hidden;
}

main {
  overflow: hidden;
}

a {
  text-decoration: none;
}

.btn:focus {
  outline: none;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
  font-family: 600;
}

img {
  max-width: 100%;
  height: auto;
}

.blod-text {
  font-weight: 600;
}

input:focus {
  outline: none;
  box-shadow: none;
}

button:focus {
  outline: none;
  box-shadow: none;
}

.btn:focus {
  outline: none;
  box-shadow: none;
}

.theme-tagline {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  color: #231F20;
}

.theme-h1 {
  font-size: 50px;
  font-family: "CabinetGrotesk-Extrabold";
  font-weight: 800;
  color: #FD9A00;
  margin-bottom: 10px;
}

.theme-h2 {
  font-size: 35px;
  font-family: "CabinetGrotesk-Bold";
  font-weight: 600;
  margin-bottom: 10px;
}

.theme-h2 span {
  color: #FD9A00;
}

.theme-h3 {
  font-family: "CabinetGrotesk-Medium";
  font-size: 26px;
  margin-bottom: 10px;
}

.theme-h4 {
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 15px;
}

.theme-p {
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  margin-bottom: 15px;
}

.theme-p a {
  color: #FD9A00;
  font-weight: 500;
}

.theme-p a:hover {
  text-decoration: underline;
}

.theme-keypoints {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.theme-keypoints.keypoints-white {
  color: #fff;
}

.theme-keypoints li {
  width: 50%;
  position: relative;
  padding-left: 30px;
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 27px;
}

.theme-keypoints li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 5px;
  background: url(./assets/images/check.webp);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  height: 15px;
  width: 15px;
}

.theme-keypoints.keypoints-white li::before {
  background-image: url(./assets/images/check-white.webp);
}

.theme-btn-ul {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 15px 0 25px;
}

.theme-btn-ul li:not(:nth-child(1)) {
  margin-left: 20px;
}

.theme-btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 30px;
  width: fit-content;
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  font-weight: 500;
  text-transform: capitalize;
  border-radius: 5px;
  border: none;
  outline: none;
  overflow: hidden;
}

.portfolio-content .nav-link {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 30px;
  width: fit-content;
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  font-weight: 500;
  text-transform: capitalize;
  border-radius: 5px;
  border: none;
  outline: none;
  overflow: hidden;
}

.theme-btn:before {
  content: '';
  position: absolute;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  background: linear-gradient(to bottom, rgba(229, 172, 142, 0), rgba(255, 255, 255, 0.5) 50%, rgba(229, 172, 142, 0));
  transform: rotateZ(60deg) translate(-5em, 15em);
}

.theme-btn:hover::before,
.theme-btn:focus::before {
  animation: sheen 1s forwards;
}

@keyframes sheen {
  100% {
    transform: rotateZ(60deg) translate(1em, -15em);
  }
}

.theme-btn-dark {
  background-color: #231F20;
  color: #fff;
}

.theme-btn-white {
  background-color: #fff;
  color: #000;
}

.portfolio-content .nav-link {
  border: 1px solid #000;
  color: #000;
  background-color: transparent;
}

.portfolio-content .nav-tabs {
  border-bottom: none;
}

.nav-link:hover,
.nav-link.active,
.theme-btn-featured {
  background: linear-gradient(180deg, rgba(250, 152, 0, 1) 0%, rgba(213, 112, 2, 1) 100%);
  color: #000;
  border-color: transparent;
}

.btn-icon i {
    padding-left: 0.7rem;
}

.padding-sec {
  padding: 60px 0;
}

/* header start */
.header-main {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
  transition: 0.5s;
}

.header-main.scroll {
  background: #fff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
}

.header-main .topbar {
  position: relative;
  padding: 17px 0;
  z-index: 1;
}

.header-main .topbar::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background: linear-gradient(180deg, rgba(250, 152, 0, 1) 0%, rgba(213, 112, 2, 1) 100%);
  border-radius: 0 0 30px 30px;
}

.header-main .topbar .topbar-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-main .topbar .topbar-content .topbar-ul {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.header-main .topbar .topbar-content .topbar-ul li a {
  display: block;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #000;
  margin: 0 10px;
  transition: 0.3s;
}

.header-main .topbar .topbar-content .topbar-ul li a:hover {
  color: #fff;
}

.header-main .topbar .topbar-content .topbar-ul li a i {
  font-size: 20px;
}

.header-main .topbar .topbar-content .social-links li a {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  width: 20px;
  padding: 15px;
  margin: 0;
  border-radius: 50%;
  transition: 0.3s;
}

.header-main .topbar .topbar-content .social-links li a:hover {
  color: #FD9A00;
  background-color: #fff;
  transform: rotate(360deg);
}

.header-main .topbar .topbar-content .social-links li a i {
  font-size: 16px;
}

.header-main .topbar .topbar-content .topbar-ul:nth-child(2) li a {
  animation: 3s getquotebar linear infinite reverse;
  background: linear-gradient(90deg, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 1) 33%, rgba(255, 255, 255, 1) 34%, rgba(255, 255, 255, 1) 66%, rgba(0, 0, 0, 1) 66%, rgba(0, 0, 0, 1) 100%);
  background-size: calc(300% + 15px);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@keyframes getquotebar {
  0% {
    background-position: center left;
  }

  100% {
    background-position: center right;
  }
}

.header-main .topbar .topbar-content .topbar-ul:nth-child(2) li a i {
  padding-left: 5px;
}

.header-main .navbar-brand img {
  width: 180px;
}

.header-main .navbar {
  transition: 0.5s;
}

.header-main.scroll .navbar {
  padding: 5px 0;
}


.header-main .custom-nav-item .dropdown-menu {
  border-radius: 0;
  padding: 0;
  left: 50%;
  right: 0;
  display: none;
  flex-wrap: wrap;
  width: 500px;
  transform: translateX(-50%);
  background-color: #000;
}

.header-main .custom-nav-item:hover .dropdown-menu {
  display: flex;
}

.header-main .custom-nav-item .dropdown-menu ul {
  width: 50%;
}

.header-main .custom-nav-item .dropdown-menu li a {
  color: #fff;
  font-family: 'Montserrat', sans-serif;
  padding: 10px 20px;
  white-space: nowrap;
  display: block;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  background-color: #000;
  /* text-align: center; */
}

.header-main .custom-nav-item .dropdown-menu li a:hover {
  background-color: #FD9A00;
  color: #fff;
  border-color: transparent;
}

.header-main .custom-nav-item .custom-nav-link {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  text-transform: capitalize;
  font-weight: 600;
  color: #231F20;
  transition: 0.3s;
}

.header-main .custom-nav-item .custom-nav-link:hover,
.header-main .custom-nav-item .custom-nav-link.active {
  color: #FD9A00;
}

.header-main .custom-nav-item .nav-btn {
  background: linear-gradient(180deg, rgba(250, 152, 0, 1) 0%, rgba(213, 112, 2, 1) 100%);
  border-radius: 10px;
  padding: 12px 15px;
  outline: none;
  transition: 0.3s;
}

.header-main .custom-nav-item .nav-btn:hover {
  color: #000;
}

/* header end */

/* Footer Start */
.footer-brand .footer-main {
  padding: 70px 0 0;
  background-color: #231F20;
  color: #fff;
}

.footer-brand .footer-main .footer-brand img {
  display: block;
  width: 280px;
  margin-bottom: 20px;
}

.footer-brand .footer-main .contact-links {
  margin-bottom: 30px;
}

.footer-brand .footer-main .quick-links li a {
  display: block;
  font-family: 'Poppins', sans-serif;
  color: #e2e2e2;
  font-size: 14px;
  transition: 0.3s;
  margin-bottom: 8px;
}

.footer-brand .footer-main .quick-links li a:hover {
  color: #FD9A00;
}

.footer-brand .footer-main .contact-links li a {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-family: 'Poppins', sans-serif;
  color: #fff;
  font-size: 14px;
  transition: 0.3s;
  margin-bottom: 10px;
}

.footer-brand .footer-main .contact-links li a:hover {
  color: #FD9A00;
}

.footer-brand .footer-main .contact-links li a i {
  display: block;
  font-size: 18px;
  width: 40px;
}

.footer-brand .footer-main .contact-links li a span {
  display: block;
  width: calc(100% - 40px);
}

.footer-brand .footer-main .footer-form {
  background-color: rgba(253, 154, 0, 0.5);
  border-radius: 10px;
}

.footer-brand .footer-main .footer-form .form-wrapper {
  padding: 25px;
}

.footer-brand .footer-main .footer-form .input-field input,
.footer-brand .footer-main .footer-form .input-field textarea {
  width: 100%;
  background-color: transparent;
  border: none;
  outline: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  font-family: 'Poppins', sans-serif;
  color: #fff;
  font-size: 14px;
  padding: 5px;
  margin-bottom: 10px;
}

.footer-brand .footer-main .footer-form .input-field textarea {
  height: 120px;
}

.footer-brand .footer-main .footer-form .input-field *:focus {
  border-color: #fff;
}

.footer-brand .footer-main .footer-form .input-field *::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.footer-brand .footer-main .footer-form .theme-btn {
  width: 100%;
}

.footer-brand .footer-main .copyright {
  padding: 20px 0;
  text-align: center;
  margin-top: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-brand .footer-main .copyright ul {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-brand .footer-main .copyright ul li a {
  display: block;
  color: #fff;
  text-decoration: underline;
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  text-transform: capitalize;
  margin-left: 10px;
}

.footer-brand .footer-main .copyright ul li a:hover {
  color: #FD9A00;
}

.footer-brand .footer-main .copyright .theme-p {
  margin: 0;
}

.footer-brand .footer-main .disclaimer {
  background: linear-gradient(180deg, rgba(250, 152, 0, 1) 0%, rgba(213, 112, 2, 1) 100%);
  padding: 10px 0;
}

.footer-brand .footer-main .disclaimer .theme-p {
  margin: 0;
}

/* Footer End */

.home-banner {
  position: relative;
  background-image: url(./assets/images/banner-1.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top;
  padding: 150px 0 150px;
}

.home-banner .scroll-down {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px;
  width: 30px;
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  color: #000;
  border: 2px solid #000;
  border-radius: 50%;
  overflow: hidden;
}

.home-banner .scroll-down:hover i {
  animation: 1s scrolldown linear infinite;
}

@keyframes scrolldown {
  0% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(25px);
  }

  41% {
    transform: translateY(-25px);
  }

  100%,
  80% {
    transform: translateY(0);
  }
}

.home-banner .banner-brands {
  display: block;
  width: 320px;
  margin-bottom: 15px;
}

.home-banner .theme-p .icons {
  color: #FD9A00;
}

.banner-form {
  position: relative;
  background-color: rgb(35, 31, 32, 0.8);
  color: #fff;
  padding: 30px 45px 50px;
  backdrop-filter: blur(5px);
}

.banner-form .theme-h3,
.banner-form .theme-p {
  text-align: center;
}

.banner-form .input-field input,
.banner-form .input-field textarea {
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  background-color: transparent;
  border: none;
  outline: none;
  color: #fff;
  padding: 10px 10px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  width: 100%;
  resize: none;
  margin-bottom: 10px;
}

.banner-form .input-field textarea {
  height: 120px;
}

.banner-form .input-field *::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.banner-form button {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%) translateY(50%);
  padding: 12px 45px;
}

.logobar {
  background-color: #000;
  color: #fff;
  padding: 30px 0;
}

.logobar .theme-p {
  font-size: 14px;
  margin: 0;
}

.logobar .theme-p span {
