  background-color: #fff;
  color: black;
}

.flip-card-back h4 {
  font-family: "Roboto", sans-serif;
  font-size: 21px;
}

.flip-card-back p {
  font-size: 14px;
  font-family: "Roboto", sans-serif;
  margin-top: 10px;
}

/* Style the back side */
.flip-card-back {
  background-color: #000;
  color: white;
  transform: rotatex(180deg);
}

